ol {
    list-style: none;
    counter-reset: item;
}

li {
    counter-increment: item;
    margin-bottom: 5px;
}

li:before {
    margin-right: 10px;
    content: counter(item);
    border-radius: 100%;
    border: 1px solid #75E7D9;
    color: #75E7D9;
    width: 1.2em;
    text-align: center;
    display: inline-block;
    position: absolute;
    left: 2.6em;
  }

.usefulQuestion {
